<template>
	<el-container>
		<el-aside width="auto" :class="{ isCollapse: isCollapse }">
			<common-aside />
		</el-aside>
		<el-container>
			<el-header style="height: 70px; border-radius: 0 35px 35px 0">
				<common-header />
			</el-header>
			<el-main>
				<div class="main-container">
					<router-view />
					<!-- <keep-alive></keep-alive> -->
				</div>
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
    import { mapState } from 'vuex'
    import CommonAside from '@/components/CommonAside.vue'
    import CommonHeader from '@/components/CommonHeader.vue'
    export default {
    	components: {
    		CommonAside,
    		CommonHeader
    	},
    	computed: {
    		...mapState({
    			isCollapse: state => state.tab.isCollapse
    		})
    	},
    	data() {
    		return {
    			timer: false
    		}
    	},
    	created() {
    		this.resize(document.body.clientWidth, document.body.clientHeight)
    		window.onresize = () => {
    			// 为了避免频繁触发resize函数导致页面卡顿，使用定时器
    			if (!this.timer) {
    				this.resize(document.body.clientWidth, document.body.clientHeight)
    				this.timer = true
    				setTimeout(() => {
    					this.timer = false
    				}, 200)
    			}
    		}
    	},
    	methods: {
    		resize(clientWidth, clientHeight) {
    			if (clientWidth < 1280 || clientWidth <= clientHeight) {
    				this.$store.commit('toggleCollapse', true)
    			} else {
    				this.$store.commit('toggleCollapse', false)
    			}
    		}
    	}
    }
</script>

<style lang="scss" scoped>
    .el-container {
    	height: 100%;
    }
    .el-aside.isCollapse {
    	border-radius: 0 0 35px 35px;
    	overflow-x: hidden;
    }
    .el-header {
    	background-color: $theme-color;
    	color: #fff;
    	padding: 0 20px 0 10px;
    }
    .el-main {
    	background: $theme-color;
    	padding: 0;
    	.main-container {
    		width: 100%;
    		height: 100%;
    		background: #fff;
    		border-radius: 35px 0 0 0;
    		padding: 20px;
    		box-sizing: border-box;
    	}
    }
</style>
